.container {
  width: 100%;
  margin-top: 150px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__content {
  }
}

.bell-icon {
  width: 70px;
  // cursor: pointer;
  position: relative;

  &__circle {
    position: absolute;
    left: 38%;
    bottom: -5%;

    height: 15px;
    width: 30px;
    background: #4c5158;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
  }
}

.notification {
  background: rgba(255, 82, 82, 1);
  // position: absolute;
  // right: 5px;
  // top: 6px;
  z-index: 2;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  transform: scale(1);
  box-shadow: 0 0 0 0 rgba(255, 82, 82, 1);
  animation: pulse-red 2s infinite;
}

@keyframes pulse-red {
  0% {
    transform: scale(0.85);
    box-shadow: 0 0 0 0 rgba(255, 82, 82, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 25px rgba(255, 82, 82, 0);
  }

  100% {
    transform: scale(0.85);
    box-shadow: 0 0 0 0 rgba(255, 82, 82, 0);
  }
}

/* Tooltips */
.tooltip {
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  padding: 0.4rem 0.6rem;
  border-radius: 40px;
  font-size: 0.4rem;
  font-weight: bold;
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  transform: translate(-50%, -100%);
  transition: all 0.3s ease;
  z-index: 1;

  &:after {
    display: block;
    position: absolute;
    bottom: 1px;
    left: 50%;
    width: 0;
    height: 0;
    content: "";
    border: solid;
    border-width: 10px 10px 0 10px;
    border-color: transparent;
    transform: translate(-50%, 100%);
  }
}
